 duy sáng tạo của học sinh, Kỳ thi HKIMO còn là cơ hội để các bạn trẻ thử thách chính bản thân mình và mở rộng mối quan hệ giao lưu văn hóa quốc tế.\n                    </p> <p data-v-4cd2ea0b>\n                      Đồng hành cùng các con là tinh thần đầy nhiệt huyết, tận tâm của các cô giáo URI, với mong muốn cuộc thi không chỉ mang lại\n                      cho các con những kiến thức quan trọng về Toán học mà còn giúp các con luôn tự tin và vững vàng khi đối mặt với những thử\n                      thách, luôn năng động, tư duy trong các hoạt động học tập.\n                    </p></div></div></div> <div class=\"dsmall-item side-three\" data-v-4cd2ea0b><div data-aos=\"fade-up-cus\" class=\"sidebar\" data-v-4cd2ea0b><div class=\"side-fixed\" data-v-4cd2ea0b><div class=\"side-fixed-wrap\" data-v-4cd2ea0b><div id=\"m_category_list-2\" class=\"widget news-sidebar widget_m_category_list\" data-v-4cd2ea0b><div class=\"sidebar-block\" data-v-4cd2ea0b><div class=\"sidebar-head\" data-v-4cd2ea0b><img width=\"15\" height=\"15\" src=\"data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%2015%2015'%3E%3C/svg%3E\" alt data-lazy-src=\"https://ischool.vn/template/assets/images/icon-dm.svg\" class=\"icon\" data-v-4cd2ea0b><noscript data-v-4cd2ea0b><img width=\"15\" height=\"15\" src=\"https://ischool.vn/template/assets/images/icon-dm.svg\" alt class=\"icon\" data-v-4cd2ea0b></noscript> <p class=\"sidebar-title\" data-v-4cd2ea0b>Danh mục</p></div> <div class=\"sidebar-body\" data-v-4cd2ea0b><div class=\"sidebar-menu\" data-v-4cd2ea0b><div class=\"sidebar-menu-list\" data-v-4cd2ea0b></div></div></div></div></div></div> <div class=\"side-close\" data-v-4cd2ea0b><i class=\"fas fa-times close icon\" data-v-4cd2ea0b></i></div></div> <div class=\"side-open\" data-v-4cd2ea0b><div class=\"side-open-wrap\" data-v-4cd2ea0b><img width=\"15\" height=\"15\" src=\"data:image/svg+xml,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20viewBox='0%200%2015%2015'%3E%3C/svg%